/****************************/
/* button                   */
/****************************/
button, .btn {
  position: relative;
  display: inline-block;
  padding: 20px 30px;
  min-height: 60px;
  border-radius: 0;
  background: #f5f5f5;
  text-align: center;
  font-size: 1.125rem;
  cursor: pointer; }
  button:hover, .btn:hover {
    background-color: #ddd;
    transition: 0.3s; }
  button.color, .color.btn {
    background-color: #373737;
    color: #fff; }
    button.color:hover, .color.btn:hover {
      background-color: #252525; }
  button.border, .border.btn {
    border: 1px solid #ccc;
    background-color: transparent; }
    button.border.color, .border.color.btn {
      color: #373737;
      background-color: transparent;
      border-color: #373737; }
    button.border:hover, .border.btn:hover {
      background-color: #676767;
      border-color: #252525;
      color: #252525; }
  button.round, .round.btn {
    border-radius: 30px; }
  button.action, .action.btn {
    background-color: #373737;
    background-image: url(data:image/png;base64,);
    background-position: right 30px center;
    background-repeat: no-repeat;
    padding-right: 127px;
    color: #fff; }
    button.action:hover, .action.btn:hover {
      background-color: #252525; }
  button.size_s, .size_s.btn {
    min-height: 45px;
    padding: 6px 20px;
    font-size: 0.813rem; }
  button.size_m, .size_m.btn {
    padding: 6px 30px; }
  button.size_l, .size_l.btn {
    min-height: 75px;
    padding: 12px 60px;
    font-size: 1.25rem; }

.btn {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  line-height: 58px; }
  .btn.size_s {
    line-height: 43px; }
  .btn.size_l {
    line-height: 73px; }

.btn_wrap {
  margin-top: 30px; }

/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
header {
  position: relative;
  height: 50px;
  margin-bottom: 20px; }
  header .logo {
    position: absolute;
    left: 50px; }
  header .menu {
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #ccc;
    display: inline-block;
    width: 30px;
    height: 30px; }

nav {
  position: absolute;
  left: 0;
  top: 40px;
  display: none; }
  nav ul {
    font-size: 1rem; }
    nav ul:after {
      content: '';
      display: block;
      clear: both; }
    nav ul li {
      float: left;
      margin-right: 20px; }
      nav ul li a {
        text-transform: uppercase; }
        nav ul li a.active {
          font-weight: bold; }

/****************************/
/* grid                     */
/****************************/
.colbox {
  display: flex;
  flex-wrap: wrap; }
  .colbox > div {
    margin: 2rem; }
  .colbox.col2 > div {
    width: calc(50% - 2rem); }
    .colbox.col2 > div:nth-child(odd) {
      margin-left: 0; }
    .colbox.col2 > div:nth-child(even) {
      margin-right: 0; }
  .colbox.col3 > div {
    width: calc(33.333% - 2.68456rem); }
    .colbox.col3 > div:nth-child(3n-2) {
      margin-left: 0; }
    .colbox.col3 > div:nth-child(3n) {
      margin-right: 0; }
  .colbox.col4 > div {
    width: calc(25% - 3.00752rem); }
    .colbox.col4 > div:nth-child(4n-3) {
      margin-left: 0; }
    .colbox.col4 > div:nth-child(4n) {
      margin-right: 0; }
  .colbox.col5 > div {
    width: calc(20% - 3.2rem); }
    .colbox.col5 > div:nth-child(5n-4) {
      margin-left: 0; }
    .colbox.col5 > div:nth-child(5n) {
      margin-right: 0; }
  .colbox.col6 > div {
    width: calc(16.66% - 4rem); }
    .colbox.col6 > div:nth-child(6n-5) {
      margin-left: 0; }
    .colbox.col6 > div:nth-child(6n) {
      margin-right: 0; }
  .colbox > .colbox {
    margin-top: 0;
    margin-bottom: 0; }

/****************************/
/* Guide                    */
/****************************/
.guide .group {
  margin: 30px 0; }
  .guide .group h4 {
    margin-bottom: 10px; }

.guide button, .guide .btn {
  margin: 0 10px 10px 0; }

/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
/****************************/
/* media                    */
/****************************/
@media (max-width: 768px) {
  .colbox.col3 > div,
  .colbox.col4 > div,
  .colbox.col5 > div,
  .colbox.col6 > div {
    width: calc(50% - 2rem); }
  .colbox > div {
    margin: 2rem !important; }
    .colbox > div:nth-child(odd) {
      margin-left: 0 !important; }
    .colbox > div:nth-child(even) {
      margin-right: 0 !important; } }

@media (max-width: 480px) {
  html {
    font-size: 90%;
    transition: 0.3s; }
  .colbox.col2 > div,
  .colbox.col3 > div,
  .colbox.col4 > div,
  .colbox.col5 > div,
  .colbox.col6 > div {
    width: 100%;
    margin: 0 0 4rem 0 !important; } }

