/****************************/
/* Color item               */
/****************************/
/****************************/
/* Mixin                    */
/****************************/
.item_wrap {
  margin: 0 auto; }
  .item_wrap:after {
    content: '';
    display: block;
    clear: both; }

.item_card {
  position: relative;
  width: 300px;
  margin-bottom: 50px; }

.card {
  position: relative;
  display: block; }
  .card .type {
    font-weight: bold;
    font-size: 1rem; }
  .card .img {
    width: 100%;
    height: 57%;
    overflow: hidden;
    background-color: #eee; }
    .card .img img {
      width: 100%;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      transition: 0.3s; }
  .card .cont {
    margin-top: 20px; }
  .card:hover img {
    -webkit-transform: scale(1);
            transform: scale(1); }

.item_view {
  position: relative;
  margin-top: 50px; }
  .item_view .inner {
    max-width: 1024px;
    margin: 0 auto; }
  .item_view .visual {
    margin-bottom: 20px; }
    .item_view .visual img {
      width: 100%; }
  .item_view .desc {
    margin-bottom: 20px; }
  .item_view .close {
    display: inline-block;
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    top: 20px;
    right: 20px;
    background-color: rgba(0, 0, 0, 0.4); }
    .item_view .close:before, .item_view .close:after {
      content: '';
      display: inline-block;
      position: absolute;
      width: 50%;
      height: 2px;
      top: 50%;
      left: 0;
      right: 0;
      margin: auto;
      border-radius: 5px;
      background-color: #fff; }
    .item_view .close:before {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }
    .item_view .close:after {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); }

.filters-button-group {
  margin-bottom: 50px; }
  .filters-button-group a {
    margin-right: 30px;
    font-size: 20px;
    font-weight: bold; }
    .filters-button-group a.on {
      text-decoration: underline; }

.grid-item {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 1px solid #ccc; }

.loading_box {
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase; }
  .loading_box span {
    display: inline-block;
    -webkit-animation: loadMotion 1s ease-out infinite alternate;
            animation: loadMotion 1s ease-out infinite alternate; }

@-webkit-keyframes loadMotion {
  0% {
    opacity: 0.7;
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    opacity: 1;
    -webkit-transform: scale(1.1);
            transform: scale(1.1); } }

@keyframes loadMotion {
  0% {
    opacity: 0.7;
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    opacity: 1;
    -webkit-transform: scale(1.1);
            transform: scale(1.1); } }

