
@import 'style';

.banner {
    position: relative;
    display: block;
    padding: 130px 90px;
    margin-bottom: 20px;
    background-color: #7c7c7c;

    * {
        color: #fff;
    }

    .cate {
        font-size: 1.25rem;
        margin-bottom: 27px;
    }
    .tit {
        font-size: 3.438rem;     
        line-height: 1.2;   
        margin-bottom: 20px;
    }

    .cont {
        font-size: 1.125rem;
        line-height: 1.5;
    }
}
