
@import 'style';

.item_wrap{
    @include clear; 
    margin: 0 auto;
}

.item_card {
    position: relative;
    width: 300px;
    margin-bottom: 50px;
    // float:left;
}
.card{
    position: relative;
    display: block;

    .type{
        font-weight: bold;
        font-size: 1rem;
    }
    .img{
        width:100%;
        height: 57%;
        overflow: hidden;
        background-color: #eee;
        img{
            width:100%;
            transform:scale(1.1);
            transition: $speed;
        }
    }
    .cont{
        margin-top: 20px;
    }

    &:hover{
        img{
            transform:scale(1);
        }
    }
}

.item_view{
    position: relative;
    margin-top: 50px;

    // position: fixed;
    // width: 100% !important;
    // height: 100%;
    // background: #fff;
    // z-index: 999;
    // top:0;
    // left:0;
    // overflow-y: auto;

    .inner{
        max-width: 1024px;
        margin: 0 auto;
    }

    .visual{
        margin-bottom: 20px;
        img{
            width:100%;
        }
    }
    .desc{
        margin-bottom: 20px;
    }
    .close{
        display: inline-block;
        position: absolute;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        top: 20px;
        right: 20px;
        background-color: rgba(0,0,0,.4);
        &:before,
        &:after{
            content: '';
            display: inline-block;
            position: absolute;
            width: 50%;
            height: 2px;
            top: 50%;
            left: 0;
            right: 0;
            margin: auto;
            border-radius: 5px;
            background-color: #fff;
        }
        &:before{
            transform: rotate(45deg);
        }
        &:after{
            transform: rotate(-45deg);
        }
    }
}

.filters-button-group{
    margin-bottom: 50px;

    a {
        margin-right: 30px;
        font-size: 20px;
        font-weight: bold;

        &.on{
            text-decoration: underline;
        }
    }
}
.grid-item{
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 1px solid #ccc;
}


.loading_box{
    background-color: rgba(255,255,255,.9);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 100%;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;

    span{
        display: inline-block;
        animation: loadMotion 1s ease-out infinite alternate;
    }
}

@keyframes loadMotion{
    0%{
        opacity: 0.7;
        transform: scale(1);
    }
    100%{
        opacity: 1;
        transform: scale(1.1);
    }
}